import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from "axios";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";

const Banner = () => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  const [banner, setbanner] = useState([]);
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;
  const getbanner = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/getalldiscountbanner",
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );
    if ((res.status = 200)) {
      ////console.log(res);
      setbanner(res.data?.banner);
    }
  };
  useEffect(() => {
    getbanner();
  }, []);
  return (
    <div className="p-10">
      <Slider {...settings} className="px">
        {banner.map((banner, index) => (
          <div key={index} className="banner-container">
            <a href={banner.link}>
              <img
                className="offerImages object-cover"
                src={`https://majesticmall.net/image/${banner.banner}`}
                alt={`offer-${index}`}
                style={{
                  height: "300px",
                  backgroundPosition: "center",
                  backgroundSize: "cover",
                  width: "100%",
                  backgroundRepeat: "no-repeat",
                }}
                loading="lazy"
                onError={(e) => {
                  e.target.src = "../img/example.jpg"; // Replace with your default image URL
                }}
              />
            </a>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default Banner;
